.title-bar {
    display: block;
    width: 100%;
    margin: 0;
    padding: 0;
    height: 3.2815em;
}

.title-bar .UL {
    display: flex;
    list-style-type: none;
    margin: 0;
    padding : 0;
    overflow: visible;
    width: 100%;
    justify-content: center;
    background-color: rgb(0, 161, 126);
    height: 3.2815em;

    
}

.title-bar button {
    font-family: inherit;
    font-size: 100%;
    background-color: inherit;
    border: none;
    color: inherit;
    margin: 0;
    padding: 0;
}

.title-bar button:hover{
   cursor:pointer;
}

.title-bar .LI {
    width: auto;
    float: right;
    text-align: center;
    text-decoration: none;
    padding: 1em .5em;
    margin: 0em 1em;
    color: white;
    height: 1.25em;
}

.title-bar .LI a{
    text-decoration: none;
    color: white;
}

.title-bar.LI:hover {
    border-bottom: solid white .075em;
    cursor: pointer;
}

.title-bar.LI.active {
    border-bottom: solid white .075em;
    font-weight: bold;
}

.title-bar.LI.Title:hover {
    border-bottom: none;
    cursor: default;
}

.title-bar .LI.Icon {
    display: inline-block;
    text-align: center;
    text-decoration: none;
    padding: 1em .5em;
    margin: 0em 1em;
    color: white;
    height: 1.25em;
}

.title-bar .LI.Title.Mobile {
    display: block;
    float: none;
}

.title-bar .LI.Mobile {
    display: block;
    float: none;
}

.title-bar .LI.Icon.Mobile {
    display: block;
    float: none;
}

.title-bar.LI.Title {
    float: none;
    text-align: center;
    text-decoration: none;
    padding: 1em .5em;
    margin: 0em 1em;
    color: white;
    height: 1.25em;
    font-weight: bold;
}

@media screen and (max-width:68.750em) {
    .title-bar .UL {
        display: flex;
        list-style-type: none;
        margin: 0;
        padding : 0;
        overflow: visible;
        width: 100%;
        justify-content: center;
        background-color: rgb(0, 161, 126);
        height: 3.2815em;
    
        
    }
}

@media print  {
    .title-bar .UL {
        display: flex;
        list-style-type: none;
        margin: 0;
        padding : 0;
        overflow: visible;
        width: 100%;
        justify-content: center;
        background-color: rgb(0, 161, 126);
        height: 3.2815em;
    
        
    }
}