.cover {
    width: 100%;
    height: 20em;
    background-color: rgb(0, 206, 161);
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
}

@media screen and (max-width:68.750em) {
    .cover {
        display: none;
    }
}
@media print {
    .cover {
        display: none;
    }
}