.FooterBar{
    width: 100%;
    position: relative;
    bottom: 1;
    background-color:rgb(0, 161, 126);
    height: 4em;
    text-align: center;
    margin:auto;
    box-shadow: 0px -4px 5px 0px rgba(0,0,0,0.35);
}

.FooterBar p {
     text-align: center;
    vertical-align: middle;
    line-height: 2em;
    color: white;
}

.FooterBar ul{
    list-style: none;
   
    width: auto;
    height: auto;
    margin: 0;
}

.FooterBar a{
    display:block;
    margin: auto;

}

.FooterBar li{
    display: inline-block;

}