.Card {
    display:block;
    z-index: 1;
    width: 70%;
    height: auto;
    position: absolute;
    left: 0;
    right: 0;
    top: 10em;
    margin-left: auto;
    margin-right: auto;
    text-align:center;
    margin-bottom: 2em;
    background-color: white;
    box-shadow: 0 3px 6px rgba(0,0,0,0.16), 0 3px 6px rgba(0,0,0,0.23);
}

.tooltip {
    position: fixed;
    z-index: 2;
    width: auto;
    height: auto;
    padding: .4em;
    border-radius: 1em;
    color: rgb(0, 206, 161);
    background-color:rgba(56, 56, 56, .7);
    text-align:center;
    display: none;
}

.Card-Header {
    position: relative;
    margin:0;
    width: 100%;
    height: auto;
    background-color: rgb(56, 56, 56);
    border-bottom: solid rgb(207, 207, 207) .05em;
}

.Card-Header.Container {
    display: flex;
    flex-direction: row;
    flex-wrap: nowrap;
    justify-content: flex-start;
    align-content: stretch;
}

.Card-Content {
    position: relative;
    width: 100%;
    height: auto;
    padding-bottom: 5em;
    background-color: white;
}


.Card-Main {
    width:80%;
    padding-top: 3em;
    margin:auto;
    text-align: left;
}

.Card-Main h3 {
   color: rgb(75, 75, 75);
   text-transform: uppercase;
   font-weight: bold;
   border-bottom: rgb(200, 200, 200) solid .1em;
   padding-bottom: .5em;
  
}

.Card-Main-Flex {
    display:flex;
    text-align: left;
    justify-content: space-between;
}

.Experience {
    width:70%
}

.Experience ul{
    list-style: none;
    margin: 0;
    padding: 0;
    list-style-type: none;
}

.Skills {
    width:25%
}

.Skills ul{
    list-style: none;
    margin: 0;
    padding: 0;
    list-style-type: none;
}

.Skills strong {
    margin-bottom: 1em;
    margin-top: 1em;
    display:block;
}

a:visited {
    color: initial;
}

.Skills li{
    list-style: none;
    margin: 0;
    color: rgb(56, 56, 56);

}

.Card-Main p {
    color: rgb(56, 56, 56);
    font-family: inherit;
    margin-bottom: 1em;
 }

.Card-Header-Image {
    display:block;
    width: 15em;
    height: 15em;
    margin: 0;
    padding:0;
    
}

.Card-Header-Image img {
    width: 15em;
    height: 15em;
}

.Card-Header-Description {
    display:block;
    margin: 0;
    padding:0;
    width: 100%;
}

.Card-Header-Content {
    display:block;
    margin: 1.5em 2.5em;
    padding:0;
    text-align: left;
    width: 15em;
}

.projectLI {
    margin-bottom: 1em;
}

.Card-Header-Content h1 {
    display:block;
    font-family:  inherit;
    color: rgb(253, 253, 253);
    margin:0;
}

.Card-Header-Content h2 {
    display:block;
    font-family:  inherit;
    color: rgb(172, 172, 172);
    margin:0;
}

.Card-Header-Content h4 {
    display:block;
    font-family:  inherit;
    color: rgb(172, 172, 172);
    margin:0;
}

.Left-Icon-Text {
    width: auto;
    height: auto;
    overflow: auto;
    margin: 0;
    display: flex;
    justify-content: flex-start;
    margin-top: .35em;
}

.Left-Icon-Text img {
    margin-right: .5em;
    margin-top: .15em;
}

.Left-Icon-Text a {
    text-decoration: none;
}


@media screen and (max-width:68.750em) {
    .Card {
        display:block;
        z-index: 1;
        width: 100%;
        height: auto;
        position: relative;
        left: 0;
        right: 0;
        top: 0em;
        margin-left: auto;
        margin-right: auto;
        text-align:center;
        margin-bottom: 2em;
        background-color: white;
        box-shadow: 0 3px 6px rgba(0,0,0,0.16), 0 3px 6px rgba(0,0,0,0.23);
    }

    .Card-Header.Container {
        display: flex;
        flex-direction: column;
        flex-wrap: nowrap;
        justify-content: center;
        align-content: stretch;
        align-items: center;
        padding: 1em 0;
    }

    .Card-Header-Content {
        display: block;
        padding: 0;
        text-align: center;
        width: 15em;
        margin: auto;
    }

    .Left-Icon-Text {
        width: auto;
        height: auto;
        overflow: auto;
        margin: 0;
        display: flex;
        justify-content: center;
        margin-top: .35em;
    }

    .Card-Main-Flex {
        display: block;
        text-align: center;
        justify-content: space-between;
    }
    
    .Skills {
        width: 100%;
    }
    .Experience {
        width: 100%;
    }

    .Card-Main {
        padding: 3em 2em 0 2em;
        margin: auto;
        text-align: left;
    }

    .projectLI {
        margin-bottom: 2.5em;
    }

    .Left-Icon-Text img {
        margin: auto .5em auto 0;
    }
    

}

